// RESET
// =================================================

// Global reset of paddings, margins, border and font related attributes
html, body, div, span, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, address, em, img, strong, sub, sup,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details,
figure, figcaption, footer, header, hgroup,
menu, nav, output, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

// HTML5 display-role reset for older browsers
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

// Removes borders around hyperlink images
a img, a:link img, a:visited img {
  border: none;
}

// Removes any style from ordered and unordered lists
ol, ul {
  list-style-type: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

// Sets default values for text alignment
caption, th, td {
  text-align: left;
}

// Defines table vertical alignment
table, td, th {
  vertical-align: middle;
}

blockquote, q {
  quotes: none;
}

blockquote::before, blockquote::after,
q::before, q::after {
  content: '';
  content: none;
}

// Defines typography defaults
em {
  font-style: italic;
}

strong {
  font-weight: bold;
}

sub, sup { font-size: 45%; line-height: 0; position: relative; vertical-align: baseline; }
sup { top: -0.65em; }
sub { bottom: -0.25em; }
